.Tags {
  display: flex;
  flex-direction: row;
}

.SingleTag {
  background-color: #ff6060;
  border-radius: 10px;
  color: white;
  font-size: 14px;
  margin-right: 10px;
  width: 115px;
  height: 25px;
  text-align: center;
}

@media screen and (max-width: 850px) {
  .SingleTag {
    width: 84px;
    height: 18px;
    font-size: 10px;
    border-radius: 5px;
    padding-bottom: 4px;
  }
}
