.Footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: black;
  color: white;
  text-align: center;
  font-size: 24px;
  padding-top: 66px;
  padding-bottom: 30px;
  width: 100vw;
}

.Logo {
  margin-bottom: 30px;
}

@media screen and (max-width: 767px) {
  .Footer {
    font-size: 12px;
  }

  .Logo {
    width: 122px;
    height: 40px;
  }
}
