html {
  min-height: 100vh;
}

body {
  position: relative;
  box-sizing: border-box;
  margin: 0 auto;
  min-height: 100vh;
  width: 100vw;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: 142.6%;
  overflow-x: hidden;
  padding-bottom: 209px;
}

a {
  text-decoration: none;
  list-style-type: none;
  cursor: pointer;
  color: #ff6060;
}

a:hover {
  text-decoration: underline;
  text-decoration-thickness: 1px;
}
