@keyframes progressive {
  0% {
    transform: translateY(10px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.Dropdown {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 47px;
  background-color: #ff6060;
  color: white;
  margin-top: 38px;
  border-radius: 5px;
  top: 0;
}

.Dropdown img {
  margin-right: 18px;
}

.DropdownTitle {
  margin-left: 18px;
}

.ArrowUp {
  transform: rotate(180deg);
  transition: transform ease-in-out 250ms;
  cursor: pointer;
}

.Arrow {
  transform: rotate(0deg);
  transition: transform ease-in-out 250ms;
  cursor: pointer;
}

.DropdownContent {
  width: 100%;
  height: auto;
  color: #ff6060;
  line-height: 34px;
  font-weight: 400;
  padding: 30px 0;
  background-color: #f6f6f6;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  animation: progressive 300ms ease-in-out both;
  transition: 0.3s ease-in-out;
  list-style: none;
}

.DropdownContent p {
  margin: 0 18px;
}

@media screen and (max-width: 1240px) {
  .Dropdown,
  .DropdownContent {
    width: 700px;
  }
}

@media screen and (max-width: 850px) {
  .Dropdown {
    width: 335px;
    height: 30px;
    margin-top: 20px;
  }

  .DropdownTitle {
    font-size: 13px;
    font-weight: 500;
  }

  .Dropdown img {
    width: 9px;
    height: 15px;
    margin: 0 12px;
  }

  .DropdownContent {
    width: 335px;
    font-size: 12px;
  }

  .DropdownContent {
    background-color: #f6f6f6;
  }

  .DropdownTitle {
    margin-left: 11px;
  }
}
